import React, { Component } from "react";
import { Link } from "react-router-dom";
import Sidebar from "../Layout/Sidebar";
import Spinner from "../Layout/spinner";
import PropTypes from "prop-types";
import { compose } from "redux";
import { connect } from "react-redux";
import { isEmpty, isLoaded, firestoreConnect } from "react-redux-firebase";

class Income extends Component {
  state = {
    totalIncome: null,
    totalExpense: null,
    NumberOfDays: null,
  };

  static getDerivedStateFromProps(props, state) {
    const { Incomes } = props;
    if (Incomes) {
      const total = Incomes.reduce((total, income) => {
        return (
          total +
          parseFloat(income.movieIncome.toString()) +
          parseFloat(income.cardIncome.toString()) +
          parseFloat(income.softwareIncome.toString()) -
          parseFloat(income.Expense.toString())
        );
      }, 0);
      const totalExpense = Incomes.reduce((total, income) => {
        return total + parseFloat(income.Expense.toString());
      }, 0);
      const NumberOfDays = Incomes.reduce((total, income) => {
        return total + 1;
      }, 0);

      return {
        totalExpense: totalExpense,
        totalIncome: total,
        NumberOfDays: NumberOfDays,
      };
    }

    return null;
  }
  render() {
    const { Incomes } = this.props;
    const { totalIncome } = this.state;

    if (!isLoaded(Incomes)) {
      return <Spinner></Spinner>;
    }

    if (isEmpty(Incomes)) {
      return (
        <div>
          Income List Is Empty
          <div className="col-6">
            <Sidebar></Sidebar>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <div className="row mx-auto">
            <div className="col-md-3 col-sm-6 col-xm-6 mb-3">
              <div className="bg-white shadow border-0">
                <div className="row  mb-2">
                  {" "}
                  <div className="col-4 bg-success">
                    <h1 className="display-4 text-center ">
                      <i className="fas fa-file-invoice-dollar text-white text-center"></i>
                    </h1>
                  </div>
                  <div className="col-8 text-center mt-2 text-secondary">
                    {" "}
                    Total Income <br></br>$
                    <strong>
                      {parseFloat(this.state.totalIncome).toFixed(2)}
                    </strong>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 col-sm-6 col-xm-6 mb-3">
              <div className="bg-white shadow border-0">
                <div className="row  mb-2">
                  {" "}
                  <div className="col-4 bg-warning">
                    <h1 className="display-4 text-center">
                      <i className="fas fa-shopping-cart text-white text-center"></i>
                    </h1>
                  </div>
                  <div className="col-8 text-center mt-2 text-secondary">
                    {" "}
                    Total Expense<br></br> $
                    <strong>
                      {parseFloat(this.state.totalExpense).toFixed(2)}
                    </strong>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 col-sm-6 col-xm-6 mb-3">
              <div className="bg-white shadow border-0">
                <div className="row  mb-2">
                  {" "}
                  <div className="col-4 bg-secondary">
                    <h1 className="display-4 text-center">
                      <i className="fas fa-funnel-dollar text-white text-center"></i>
                    </h1>
                  </div>
                  <div className="col-8 text-center mt-2 text-secondary">
                    {" "}
                    Total Profit<br></br> $
                    <strong>
                      {(
                        parseFloat(this.state.totalIncome) -
                        parseFloat(this.state.totalExpense)
                      ).toFixed(2)}
                    </strong>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 col-sm-6 col-xm-6 mb-3">
              <div className="bg-white shadow border-0">
                <div className="row  mb-2">
                  {" "}
                  <div className="col-4 bg-info">
                    <h1 className="display-4 text-center">
                      <i className="fas fa-calendar-alt text-white text-center"></i>
                    </h1>
                  </div>
                  <div className="col-8 text-center mt-2 text-secondary">
                    {" "}
                    # of days<br></br>
                    <strong>{parseFloat(this.state.NumberOfDays)}</strong>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="card row mt-3 bg-light border-0 shadow">
            <div className="m-2">
              <p className="display-5 float-left">
                <i className="fas fa-file-invoice-dollar mr-2 text-secondary"></i>
                Income List
                {/* <span className="badge badge-secondary m-2">
                    {Incomes.length}
                  </span>{" "} */}
              </p>
              <Sidebar className="float-right"></Sidebar>
            </div>
            <div className="row m-1">
              <table className="table table-striped table-sm">
                <thead className="thread-inverse">
                  <tr>
                    <th>Date</th>
                    <th>Income</th>
                    <th>Expense</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {Incomes.map((income) => (
                    <tr key={income.id}>
                      <td>{income.Date}</td>
                      <td>
                        $
                        {(
                          parseFloat(income.movieIncome) +
                          parseFloat(income.cardIncome) +
                          parseFloat(income.softwareIncome)
                        ).toFixed(2)}
                      </td>
                      <td> ${parseFloat(income.Expense).toFixed(2)}</td>
                      <td>
                        <Link
                          to={`/incomes/${income.id}`}
                          className="btn btn-sm btn-secondary"
                        >
                          <i className="fas fa-arrow-circle-right mr-2"></i>
                          Detail
                        </Link>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      );
    }
  }
}

Income.propTypes = {
  firestore: PropTypes.object.isRequired,
  Incomes: PropTypes.array,
};

export default compose(
  firestoreConnect([
    {
      collection: "IncomeRecords",
    },
  ]),
  connect((state, props) => ({
    Incomes: state.firestore.ordered.IncomeRecords,
  }))
)(Income);
